___CSS_LOADER_EXPORT___.push([module.id, `.loader {
    border: 6px solid #f3f3f3;
    border-top: 6px solid #4fc3f7;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
    margin: 1rem 0;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.form_container {
    width: 100%;
    background-color: #fcfcfc;
    max-width: 900px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.form__inputs {
    position: relative;
}

.form__inputs_overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: rgba(212, 188, 188, 0.329);
}

.form__inputs_overlay--active {
    z-index: 1;
}`, "",{"version":3,"sources":["webpack://./src/app.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,kCAAkC;IAClC,cAAc;AAClB;;AAEA;IACI,KAAK,uBAAuB,EAAE;IAC9B,OAAO,yBAAyB,EAAE;AACtC;;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,gBAAgB;IAChB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,6BAA6B;AACjC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,WAAW;IACX,4CAA4C;AAChD;;AAEA;IACI,UAAU;AACd","sourcesContent":[".loader {\r\n    border: 6px solid #f3f3f3;\r\n    border-top: 6px solid #4fc3f7;\r\n    border-radius: 50%;\r\n    width: 30px;\r\n    height: 30px;\r\n    animation: spin 1s linear infinite;\r\n    margin: 1rem 0;\r\n}\r\n\r\n@keyframes spin {\r\n    0% { transform: rotate(0deg); }\r\n    100% { transform: rotate(360deg); }\r\n}\r\n\r\n.form_container {\r\n    width: 100%;\r\n    background-color: #fcfcfc;\r\n    max-width: 900px;\r\n    border-radius: 5px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: space-evenly;\r\n}\r\n\r\n.form__inputs {\r\n    position: relative;\r\n}\r\n\r\n.form__inputs_overlay {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    z-index: -1;\r\n    background-color: rgba(212, 188, 188, 0.329);\r\n}\r\n\r\n.form__inputs_overlay--active {\r\n    z-index: 1;\r\n}"],"sourceRoot":""}]);
